import * as React from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { Navbar } from './components/Navbar';
import ReactEmbedGist from 'react-embed-gist';
import OpenToWorkComponent from './components/OpenToWorkComponent';
import { HelmetProvider } from 'react-helmet-async';
import Seo from './Seo';
import { StyleContainer } from './css/GistStyleComponent';

const { useState, useEffect } = React;

interface Author {
  name: string;
  image: string;
  link: string;
  position: string;
}

interface Category {
  name: string;
}
interface Gist {
  title: string;
  description: string;
  user: string;
  link: string;
  image: string;
}

interface MatchParams {
  slug: string;
}
interface GistViewerProps extends RouteComponentProps<MatchParams> {}

const Blog: React.FC<GistViewerProps> = ({ match }) => {
  const styles = {
    main: {
      backgroundColor: '#f1f1f1',
      width: '100%',
    },
    inputText: {
      padding: '10px',
      color: 'red',
    },

    gistContainer: {
      margin: '20px',
      padding: '20px',
      border: '1px solid #ccc',
    },
    gistTitle: {
      fontSize: '14px',
    }

  };
  const { slug } = match.params;
  const [gist, setGist] = useState<Gist | null>(null);
  const url = `https://raw.githubusercontent.com/aungthuoo/apis/main/blogs/${slug}/index.json`;

  console.log(slug);
  useEffect(() => {
    fetch(url)
      .then((response) => response.json())
      .then((jsonData) => setGist(jsonData))
      .catch((error) => console.error('Error fetching the JSON data:', error));
  }, []);

  // const Note: React.FC = () => {
  const code = `
        function greet() {
            console.log("Hello, World!");
        }
    `;

  // Display loading message if data is not yet fetched
  if (!gist) {
    return (
      <div className="flex items-center justify-center h-screen">
        <img
          className="rounded-full max-w-none w-10 h-10"
          src="/images/logos/softwarezay.gif"
        />
        <p className="pl-2 text-xl font-bold">Loading...</p>
      </div>
    );
  }

  return (
    <>
      <HelmetProvider>
        <Navbar />
        <Seo
          seo={{
            title: gist.title,
            description: gist.title,
            name: gist.user,
            type: 'article',
            image: gist.image,
          }}
        />
        <div className="py-5">
          <div className="max-w-3xl mx-auto overflow-hidden">
            <div className="p-6 bg-gradient-to-r from-cyan-500 to-blue-500">
              <h5 className="text-black text-sm font-bold">Developer note</h5>
              <h2 className="text-3xl text-white font-extrabold">
                {gist.title}
              </h2>
            </div>
            <StyleContainer>
              <ReactEmbedGist
                gist={`${gist.user}/${gist.link}`}
                wrapperClass="gist__bash"
                loadingClass="loading__screen"
                titleClass="gist__title"
                contentClass="styles"
                // file=".bash_profile.sh"
              />
            </StyleContainer>
          </div>
        </div>
        <OpenToWorkComponent name="" />
      </HelmetProvider>
    </>
  );
};

export default Blog;
