import * as React from 'react';
import ExperienceComponent from './components/ExperienceComponent';
import { Navbar } from './components/Navbar';
import CategoryComponent from './components/CategoryComponent';

const { useState, useEffect } = React;

interface Skill {
  name: string;
}

interface Year {
  from: string;
  to: string;
  duration: string;
}
interface Project {
  name: string;
}
interface Experience {
  name: string;
  degree: string;
  logo: string;
  slug: string;
  year: Year;
  skills: Skill[];
  projects: Project[];
  link: string;
  description: string;
  images: string[];
}

const Educations: React.VFC = () => {
  const [educations, setEducaitons] = useState<Experience[] | null>(null);

  // Fetch the JSON data on component mount
  useEffect(() => {
    fetch(
      'https://raw.githubusercontent.com/aungthuoo/apis/main/educations/index.json',
    )
      .then((response) => response.json())
      .then((jsonData) => setEducaitons(jsonData))
      .catch((error) => console.error('Error fetching the JSON data:', error));
  }, []);

  // Display loading message if data is not yet fetched
  if (!educations) {
    return (
      <div className="flex items-center justify-center h-screen">
        <img
          className="rounded-full max-w-none w-10 h-10"
          src="/images/logos/softwarezay.gif"
        />
        <p className="pl-2 text-xl font-bold">Loading...</p>
      </div>
    );
  }
  return (
    <>
      <Navbar />
      <div className="max-w-7xl mx-auto my-8 px-2">
        <div className="flex justify-center text-2xl md:text-3xl font-bold">
          Educations
        </div>
        {/* <pre>{JSON.stringify(blogs, null, 2)}</pre> */}
        <div className="py-15 pt-6">
          <div className="max-w-xl mx-auto rounded-lg overflow-hidden shadow-xs">
            {educations.map((education, index) => (
              <article className="bg-white dark:bg-gray-900 dark:text-white mb-4 break-inside break-inside-avoid rounded-xl bg-white flex flex-col bg-clip-border">
                <div className="flex p-6 items-center justify-between">
                  <div className="flex">
                    <a className="inline-block mr-4" href="#">
                      <img
                        className="rounded-full max-w-none w-14 h-14"
                        src={education.logo}
                      />
                    </a>
                    <div className="flex flex-col">
                      <div className="flex items-center">
                        <a
                          className="inline-block text-lg font-bold mr-2"
                          href="#"
                        >
                          {education.name}
                        </a>
                        {/* <span className="text-slate-500">3 minutes ago</span> */}
                      </div>
                      <div className="text-slate-800">{education.degree}</div>

                      <div className="text-slate-500">
                        <span>{education.year.from}</span> -
                        <span>{education.year.to}</span>
                      </div>
                    </div>
                  </div>
                </div>

                <p className="pr-6 pl-6 pb-6">
                  <div className="inline-block text-lg font-bold mr-2">
                    Skills :
                  </div>
                  <div className="text-slate-500">
                    {education.skills &&
                      education.skills.length > 0 &&
                      education.skills.map((category, index) => (
                        <CategoryComponent key={index} category={category} />
                      ))}
                  </div>
                </p>

                <p className="pr-6 pl-6 pb-6">
                  <div className="inline-block text-lg font-bold mr-2">
                    Projects :
                  </div>
                  <div className="text-slate-500">
                    {education.projects &&
                      education.projects.length > 0 &&
                      education.projects.map((category, index) => (
                        <CategoryComponent key={index} category={category} />
                      ))}
                  </div>
                </p>
              </article>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};
export default Educations;
