import * as React from 'react';
import CategoryComponent from './CategoryComponent';
import { Experience } from '../models/experience';
const { useState, useEffect } = React;

const ExperiencesComponent: React.VFC = () => {
  const [experiences, setExperiences] = useState<Experience[] | null>(null);

  // Fetch the JSON data on component mount
  useEffect(() => {
    fetch(
      'https://raw.githubusercontent.com/aungthuoo/apis/main/experiences/index.json',
    )
      .then((response) => response.json())
      .then((jsonData) => setExperiences(jsonData))
      .catch((error) => console.error('Error fetching the JSON data:', error));
  }, []);

  // Display loading message if data is not yet fetched
  if (!experiences) {
    return (
      <div className="flex items-center justify-center h-screen">
        <img
          className="rounded-full max-w-none w-10 h-10"
          src="/images/logos/softwarezay.gif"
        />
        <p className="pl-2 text-xl font-bold">Loading...</p>
      </div>
    );
  }
  return (
    <>
      <div className="max-w-7xl mx-auto my-8 px-2">
        <div className="flex justify-center text-2xl md:text-3xl font-bold">
          Experiences
        </div>
        {/* <pre>{JSON.stringify(blogs, null, 2)}</pre> */}
        <div className="py-15 pt-6">
          <div className="max-w-xl mx-auto rounded-lg overflow-hidden shadow-xs">
            {experiences.slice(0, 3).map((experience, index) => (
              <article className="bg-white dark:bg-gray-900 dark:text-white mb-4 break-inside break-inside-avoid rounded-xl bg-white flex flex-col bg-clip-border">
                <div className="flex p-6 items-center justify-between">
                  <div className="flex">
                    <a className="inline-block mr-4" href="#">
                      <img
                        className="rounded-full max-w-none w-14 h-14"
                        src={experience.logo}
                      />
                    </a>
                    <div className="flex flex-col">
                      <div className="flex items-center">
                        <a
                          className="inline-block text-lg font-bold mr-2"
                          href="#"
                        >
                          {experience.company}
                        </a>
                        {/* <span className="text-slate-500">3 minutes ago</span> */}
                      </div>
                      <div className="text-slate-800">
                        {experience.position}
                      </div>

                      <div className="text-slate-500">
                        <span>{experience.year.from}</span> -
                        <span>{experience.year.to}</span> .
                        <span>{experience.year.duration}</span>
                      </div>
                    </div>
                  </div>
                </div>

                <p className="pr-6 pl-6 pb-6">
                  <div className="inline-block text-lg font-bold mr-2">
                    Skills :
                  </div>
                  <div className="text-slate-500">
                    {experience.skills &&
                      experience.skills.length > 0 &&
                      experience.skills.map((category, index) => (
                        <CategoryComponent key={index} category={category} />
                      ))}
                  </div>
                </p>

                <p className="pr-6 pl-6 pb-6">
                  <div className="inline-block text-lg font-bold mr-2">
                    Projects :
                  </div>
                  <div className="text-slate-500">
                    {experience.projects &&
                      experience.projects.length > 0 &&
                      experience.projects.map((category, index) => (
                        <CategoryComponent key={index} category={category} />
                      ))}
                  </div>
                </p>
              </article>
            ))}
            <div className="flex justify-center max-w-xl ">
              <a
                href="/experiences"
                className="py-3 px-4 w-full block bg-slate-100 text-center rounded-lg hover:bg-slate-200 transition ease-in-out delay-75 cursor-pointer"
              >
                Show more experiences
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default ExperiencesComponent;
